import { SearchMethod, SearchNode, SearchParams, SearchResult, rewind } from "../";
import Queue from "../utils/Queue";

const run = ({ map, start, target }: SearchParams): SearchResult => {
  const result: SearchResult = { path: null, states: [] };
  const visited = new Set<string>([start.toString()]);
  const queue = new Queue<SearchNode>([{ pos: start }]);

  while (!queue.isEmpty()) {
    const node = queue.dequeue();

    result.states.push({ current: node, visited: new Set([...visited]) });

    if (node.pos.equals(target)) {
      result.path = rewind(node);
      break;
    }

    for (const neighbour of map.getNeighbours(node.pos)) {
      const hash = neighbour.toString();

      if (!visited.has(hash)) {
        visited.add(hash);
        queue.enqueue({ pos: neighbour, prev: node });
      }
    }
  }

  return result;
};

const bfs: SearchMethod = { name: "Breadth-first search", run };

export default bfs;
